function showCabinModal(elem, modal) {
  elem.onclick = function(ev) {
    showModal(modal, ev)
  }
}

let currentModal  = null
function showModal(modal, ev) {
  let cross = document.getElementById('crossGl')
  let lines = document.getElementById('linesGl')  
  if (currentModal) {
    hideModal(currentModal)
    currentModal = null
  }
  let width = document.documentElement.clientWidth
  document.body.style.overflow = 'hidden'
  document.body.style.paddingRight = document.documentElement.clientWidth - width + 'px'
  currentModal = modal
  currentModal.style.display = 'block'
  lines.style.display = 'none'
  cross.style.display = 'block'
  modal.onclick = function(ev) {
    let target = ev.target.closest('.closeButtGl,.contRdGl,.iconDashBlockGl')
    if (!target) return
    if (target.classList == 'closeButtGl' || target.classList.contains('iconDashBlockGl')) {
      hideModal(modal)
    } else if (target.classList == 'contRdGl') {
      ev.preventDefault()
      let input = target.querySelector('input')
      input.checked = !input.checked
    }
  }
  document.onkeydown = function(ev) {
    if (ev.key == 'Escape') {
      hideModal(currentModal)
    }
  }
  window.onclick = function(ev) {
    if (ev.target == currentModal) {
      hideModal(currentModal)
    }
  }
}
function hideModal(modal) {
  let cross = document.getElementById('crossGl')
  let lines = document.getElementById('linesGl')  
  modal.style.display = 'none'
  cross.style.display = 'none'
  lines.style.display = 'block'
  document.body.style.overflow = ''
  document.body.style.overflowY = ''
  document.body.style.paddingRight = 0 + 'px'
}

function switchLung(sw) {
  let up = sw.querySelector('.arrowUpGl')
  let down = sw.querySelector('.arrowDnGl')
  let cont = sw.querySelector('.linksSwGl')
  sw.onclick = function(ev) {
    let base = sw.querySelector('.linksSwGl > a')
    up.classList.toggle('disBlockGl')
    down.classList.toggle('nonBlockGl')
    for (let a of sw.querySelectorAll('a')) {
      a.classList.toggle('disBlockGl')
    }
    let target = ev.target.closest('a')
    if (target == base) {
      return
    } else if (target) {
      cont.prepend(target)
    } 
  }
}

function ShowNavMenu(options) {
  let modal = options.modal
  let content = modal.querySelector('#modalNavContGl')
  let navSwitch = options.navSwitch
  let navRight = options.navRight
  let navMenu = options.navMenu
  let cloneNav = navMenu.cloneNode(true)
  let cloneLogin = navRight.cloneNode(true)
  navSwitch.onclick = function(ev) {
    cloneNav.classList.add('navMobileGl')
    cloneLogin.classList.add('loginMobileGl')
    content.append(cloneNav)
    content.append(cloneLogin)
    showModal(modal, ev)
    new ModalLoginGl({
      modal: document.getElementById('modLogGl'),
      login: content.querySelector('#loginGl')
    })
  }
}

function ModalLoginGl(options) {
  let modal = options.modal
  if (!modal) return
  let login = options.login
  login.onclick = function(ev) {
    showModal(modal, ev)
  }
}

function contactModal(elems) {
  if (!elems) return
  let modal = document.getElementById('modContactGl')
  for (let elem of elems) {
    elem.onclick = function(ev) {
      ev.preventDefault()
      showModal(modal, ev)
    }
  }
}
function planModal(elems) {
  if (!elems) return
  let modal = document.getElementById('modPlanGl')
  for (let elem of elems) {
    elem.onclick = function(ev) {
      ev.preventDefault()
      showModal(modal, ev)
    }
  }
}

function widthGreenLine(elems) {
  if (!elems) return
  for (let elem of elems) {
    let line = elem.querySelector('.greenLineGl')
    let pers = elem.nextElementSibling.firstChild.innerHTML
    line.style.width = pers + '%'
  }
}

